.stats ul li button {
  background: none;
  border: 3px solid;
  border-radius: 5rem;
  font-size: 90%;
  text-transform: uppercase;
  padding: 0.25rem 2rem;
}


.stats ul li button:active {
  background: inherit;
  color: inherit;
}

/* pos defined in container */
div.stats {
  position: absolute;
}

div.stats.horizontal {
  width: 85%;
}

div.stats.vertical {
  width: 40%;
}

.stats ul {
  display: flex;
}

.stats ul.horizontal {
  align-items: center;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.stats ul.vertical {
  flex-direction: column;
  align-items: flex-end;
}

.stats ul li {
  font-size: 100%;
  text-transform: capitalize;
}

.stats.horizontal ul li {
  border-right: 1px solid;
  padding: 0 1.5rem;
  text-align: center;
}
.stats.vertical ul li {
  border-bottom: 1px solid;
  padding: 1.5rem 0;
  text-align: left;
}

.stats.horizontal ul li:last-child {
  border-right: none;
}

.stats.vertical ul li:last-child {
  border-bottom: none;
}

.stats ul li img {
  display: block;
  margin: 0 auto;
  padding: 1rem 0;
}


@media screen and (max-width: 1200px) {

  .stats ul li {
    font-size: 100%;
    width: 30%;
  }

  .stats ul li img {
   width: 80%;
  }

  .stats ul li button {
    font-size: 140%;
  }

  .stats ul.horizontal {
    align-items: baseline;
    width: 100%;
  }

  .stats ul.horizontal li {
    padding: 0 0.25rem;
  }

  .stats ul.horizontal li:nth-of-type(3) {
    border-right: none;
  }

  .stats.horizontal ul li:last-child {
    padding-top: 3rem;
  }

}
